import React from "react";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Typography from "@mui/material/Typography";
import { CardActionArea, Box } from "@mui/material";
import Modal from "@mui/material/Modal";

const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 600,
  boxShadow: 24,
  p: 4,
};

function BobaCard() {
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  return (
    <div>
      <Card sx={{ maxWidth: 350 }}>
        <CardActionArea>
          <CardMedia
            component="img"
            height="250"
            image={require("./img/thumbnail.png")}
            alt="thumbnail"
          />
          <CardContent>
            <Typography gutterBottom variant="h5" component="div">
              Boba Tracker
            </Typography>
            <Typography variant="p" color="text.secondary">
              My New Year's Resolution for 2021 was to drink less boba, as it is
              costly and bad for my health. To discourage myself from buying it
              as often, I created a webapp to track every single time I drank
              boba. That way, it would serve as a reminder/detterent from
              getting boba all the time.
            </Typography>
          </CardContent>
        </CardActionArea>
      </Card>

      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box style={style}>
          <Card>
            <Box sx={{ p: 5 }}>
              <Typography id="modal-modal-title" variant="h6" component="h2">
                Text in a modal
              </Typography>
              <Typography id="modal-modal-description" sx={{ mt: 2 }}>
                Duis mollis, est non commodo luctus, nisi erat porttitor ligula.
              </Typography>
            </Box>
          </Card>
        </Box>
      </Modal>
    </div>
  );
}

export default BobaCard;
